.films__section {
  padding-top: 60px;
  height: 100%;
  .films__content {
    .films__header {
      display: flex;
      border: none;
      justify-content: center;
      margin: 1rem;
    }
    .films__body {
      position: relative;
      padding-top: 40%;
      // .content {
      //   position: absolute;
      //   top: 0;
      //   padding-top: 40%;
      // }
      :global(.nowShowing),
      :global(.upComing) {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
      }
    }
  }
}

.films__header {
  .header__tab {
    border: none;
    font-size: 2rem;
    padding: 1rem 1.5rem;
    background-color: transparent;
    transition: 0.2s all;
    &:first-child {
      margin-right: 1rem;
    }
    &:hover {
      font-size: 2.4rem;
    }
    &:global(.active) {
      font-size: 2.4rem;
      color: #fa5238;
    }
  }
}

.films__container {
  width: 80%;
  perspective: 800px;
  padding-bottom: 2rem;
  .films__slide {
    width: 25%;
  }
}

.film__item {
  margin: 1rem;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.45) 0px 20px 20px -30px;
  .film__image {
    padding-top: 145%;
    border-radius: 4px;
    margin-top: 2rem;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    .point {
      position: absolute;
      top: 12px;
      right: 12px;
      background-color: rgba(12, 27, 54, 0.8);
      border: 1px solid #1f2e46;
      border-radius: 4px;
      color: white;
      font-size: 2rem;
      font-family: "SF Medium";
      padding: 2px 8px;
      line-height: 1.1;
      > span {
        text-align: center;
        display: block;
        margin: 1px;
      }
      > div {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .star {
        background: url("../../asset/image/star1.png") center center no-repeat;
        width: 8px;
        height: 8px;
      }

      .half {
        background: url("../../asset/image/star1.2.png") center center no-repeat;
        width: 10px;
        height: 10px;
      }
      .star,
      .half {
        background-size: contain;
        display: inline-block;
        margin: 1px;
      }
    }
  }

  .film__image {
    cursor: pointer;
    .overlay {
      opacity: 0;
      transition: 0.2s all;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      border-radius: 4px;
      background: linear-gradient(to top, #000, transparent);
      .play {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: url("../../asset/image/play-video.png") center no-repeat;
        background-size: contain;
        width: 60px;
        height: 60px;
        border: none;
        &:hover {
          opacity: 0.7;
        }
      }
    }
    &:hover .overlay {
      opacity: 1;
    }
  }

  .film__info {
    margin-top: 1rem;
    padding-bottom: 1rem;
    background-color: white;
    position: relative;
    cursor: pointer;
    .rated {
      font-size: 14px;
      background-color: #00ac4d;
      border-radius: 4px;
      padding: 0 5px;
      min-width: 33px;
      margin-right: 8px;
      text-align: center;
      color: white;
      display: inline-block;
    }
    .title {
      font-family: "SF Medium";
      font-size: 16px;
      color: black;
      line-height: 22px;
      max-width: 100%;
      display: inline-block;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .length {
      font-family: "SF Text Regular";
      margin-top: 8px;
      font-size: 1.3rem;
      position: relative;
    }
    .book {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      width: 100%;
      font-family: "SF Text Regular";
      font-size: 18px;
      width: 100%;
      border-radius: 4px;
      color: white;
      text-align: center;
      text-decoration: none;
      text-transform: uppercase;
      padding: 10px;
      border: none;
      background: linear-gradient(to left, #fb4226, #ce3017 100%);
      z-index: 10;
      &:hover {
        background: #fb4226;
      }
    }

    &:hover .rated,
    &:hover .title,
    &:hover .length {
      opacity: 0;
    }

    &:hover .book {
      opacity: 1;
    }
  }
}
